import React, { useState } from "react";
import Board from "./Board";

interface History {
	squares: (string | null)[];
}

export default function Game() {
	const [history, setHistory] = useState<History[]>([
		{ squares: Array(9).fill(null) }
	]);
	const [stepNumber, setStepNumber] = useState(0);
	const [xIsNext, setXIsNext] = useState(true);

	function calculateWinner(squares: (string | null)[]): string | null {
		const lines = [
			[0, 1, 2],
			[3, 4, 5],
			[6, 7, 8],
			[0, 3, 6],
			[1, 4, 7],
			[2, 5, 8],
			[0, 4, 8],
			[2, 4, 6]
		];
		for (let i = 0; i < lines.length; ++i) {
			const [a, b, c] = lines[i];
			if (
				squares[a] &&
				squares[a] === squares[b] &&
				squares[a] === squares[c]
			) {
				return squares[a];
			}
		}
		return null;
	}

	function handleClick(i: number): void {
		const currHistory = history.slice(0, stepNumber + 1);
		const historyNode = currHistory[currHistory.length - 1];
		const squares = historyNode.squares.slice();
		if (calculateWinner(squares) || squares[i]) {
			return;
		}
		squares[i] = xIsNext ? "X" : "O";
		setHistory(currHistory.concat([{ squares: squares }]));
		setStepNumber(currHistory.length);
		setXIsNext(!xIsNext);
	}

	function jumpTo(step: number): void {
		setStepNumber(step);
		setXIsNext(step % 2 === 0);
	}

	function status(): string {
		const winner = calculateWinner(history[stepNumber].squares);
		return winner ? `Winner: ${winner}` : `Next Player: ${xIsNext ? "X" : "O"}`;
	}

	return (
		<div className="game">
			<div className="game-board">
				<Board
					squares={history[stepNumber].squares}
					onClick={i => handleClick(i)}
				/>
			</div>
			<div className="game-info">
				<div>{status()}</div>
				<ol>
					{history.map((_step: History, move: number) => (
						<li key={move}>
							<button onClick={() => jumpTo(move)}>
								{move ? "Go to move #" + move : "Go to game start"}
							</button>
						</li>
					))}
				</ol>
			</div>
		</div>
	);
}
