import React, { useState } from "react";
import "./BoardPage.scss";
import Classes from "constants/StyleNames";
import boardData from "store/data";
import Board from "models/sud/Board";
import Square from "models/sud/Square";

export default function BoardPage(): JSX.Element {
	const [board, setBoard] = useState(boardData);

	function onClick(x: number, y: number) {
		setBoard((board: Board) => {
			const squares = board.squares;
			squares[x][y].value = (squares[x][y].value % 9) + 1;
			return { squares };
		});
	}

	function checkCorrect() {
		var isCorrect = board.squares.every((x: Square[]) => {
			var numArr = x.map(y => y.value);
			return new Set(numArr).size === numArr.length;
		});
		if (isCorrect) {
			alert("nice");
		} else {
			alert("not nice");
		}
	}

	return (
		<div className={Classes.boardPage}>
			<div className={Classes.board}>
				{board.squares.map((column: Square[], index1: number) => {
					return (
						<div key={index1} data-x={index1} className={Classes.boardColumn}>
							{column.map((square: Square, index2: number) => {
								return (
									<div
										key={index2}
										className={Classes.boardSquare}
										onClick={() => onClick(index1, index2)}
									>
										{square.value}
									</div>
								);
							})}
						</div>
					);
				})}
			</div>
			<div className="d-flex justify-content-center">
				<button onClick={checkCorrect}>CHECK</button>
			</div>
		</div>
	);
}
